/* AddToDo */
.add-to-do, .to-do-list {
  margin: 30px 0;
}

.container-center {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

/* ToDoList */
ul.to-do-list li {
  cursor: pointer;
}
ul.to-do-list li.strikethrough {
  text-decoration: line-through;
}
ul.to-do-list li.not-added {
  cursor: default;
  color: #ccc;
  border: dashed 2px #ccc;
}



